.header-component {
    width: 100%;
    color: var(--text);
    background-color: var(--bg200);
    padding: var(--padding);
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 20;
    position: fixed;
    top: 0;
}

.header-wrap > a:nth-child(1) {
    display: block;
    margin: auto 35px auto 0;
}

.header-wrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
